import React, { useEffect, useState } from "react";
import useSWR from "swr";
import { fetcher, tmdbAPI } from "apiConfig/config";
import useDebounce from "hooks/useDebounce";
import Skeleton from "loading/Skeleton";
import Banner from "components/banner/Banner";
import MovieCard from "components/movie/MovieCard";
import ReactPaginate from "react-paginate";

const Movies = () => {
  const [movies, setMovies] = useState([]);
  const [filmFilter, setFilmFilter] = useState(null);
  const [page, setPage] = useState(1);
  const [url, setUrl] = useState(tmdbAPI.getMoviePopular(page));

  const filmQuery = useDebounce(filmFilter, 1000);
  const { data, error } = useSWR(url, fetcher);
  const loading = !data || error;

  useEffect(() => {
    if (filmQuery) {
      setUrl(tmdbAPI.getMovieQuery(filmQuery, page));
    } else {
      setUrl(tmdbAPI.getMoviePopular(page));
    }
  }, [filmQuery, page]);

  useEffect(() => {
    setMovies(data?.results || []);
  }, [data?.results]);
  const handleChange = (e) => {
    setFilmFilter(e.target.value);
  };
  return (
    <div className="py-10">
      <Banner type="popular" />

      <form
        action=""
        className="w-full max-w-[600px] mx-auto flex items-center h-16 overflow-hidden my-20"
        // onSubmit={handleSearchMovie}
      >
        <input
          type="text"
          // ref={inputRef}
          onChange={handleChange}
          placeholder="Type to search..."
          className="flex-grow h-full p-4 outline-none rounded-lg rounded-r-none"
        />
        <button
          type="submit"
          className="uppercase text-white text-xl px-6 py-4 h-full bg-primary rounded-lg rounded-l-none"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth="1.5"
            stroke="currentColor"
            className="w-6 h-6"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M21 21l-5.197-5.197m0 0A7.5 7.5 0 105.196 5.196a7.5 7.5 0 0010.607 10.607z"
            />
          </svg>
        </button>
      </form>

      <PaginatedItems
        itemsPerPage={data?.results?.length}
        itemsLength={data?.total_results}
        setPage={setPage}
        page={page}
      />

      <div className="grid grid-cols-4 gap-5 max-w-[1200px] px-[30px] mx-auto mb-10">
        {!loading
          ? movies?.map((movie) => <MovieCard movie={movie} key={movie.id} />)
          : Array(8)
              .fill(null)
              .map((item, id) => <MovieLoadingItem key={id} />)}
      </div>

      <PaginatedItems
        itemsPerPage={data?.results?.length}
        itemsLength={data?.total_results}
        setPage={setPage}
        page={page}
      />
    </div>
  );
};

const MovieLoadingItem = () => {
  return (
    <div className="movie-card rounded-lg p-3 bg-slate-800 text-white h-full flex flex-col">
      <Skeleton width="100%" height="250px" borderRadius="4px" />

      <div className="flex flex-col flex-1">
        <div className="">
          <Skeleton height="20px" marginTop="10px" />
          <Skeleton height="20px" marginTop="10px" />
        </div>
        <div className="flex items-center justify-between text-sm opacity-80 mb-10 gap-x-4 mt-3">
          <Skeleton height="20px" />
          <div className="flex items-center justify-center gap-x-1">
            <Skeleton height="20px" width="10px" />
            <svg
              width="20"
              height="20"
              viewBox="0 0 46 43"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M46 16.4415H28.4009L23 0L17.5991 16.4415H0L14.3955 26.5585L8.78929 43L23 32.8106L37.2107 43L31.5942 26.5585L46 16.4415Z"
                fill="#FF9500"
              />
            </svg>
          </div>
        </div>

        {/* <button className="py-3 px-6 rounded-lg capitalize bg-primary w-full mt-auto text-center">
          watch now
        </button> */}
        <Skeleton padding="20px" />
      </div>
    </div>
  );
};

function PaginatedItems({ itemsPerPage, itemsLength, setPage, page }) {
  const [pageCount, setPageCount] = useState(0);
  const [itemOffset, setItemOffset] = useState(0);
  useEffect(() => {
    if (!itemsLength) return;
    // if (!itemsPerPage) return;
    setPageCount(Math.ceil(+itemsLength / +itemsPerPage));
  }, [itemOffset, itemsLength, itemsPerPage]);

  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % itemsLength;
    setItemOffset(newOffset);

    setPage(event.selected + 1);
  };
  return (
    <>
      <ReactPaginate
        breakLabel="..."
        nextLabel=">>"
        onPageChange={handlePageClick}
        pageRangeDisplayed={3}
        marginPagesDisplayed={1}
        pageCount={pageCount}
        previousLabel="<<"
        renderOnZeroPageCount={null}
        className="pagination"
        forcePage={page - 1}
      />
    </>
  );
}

export default Movies;
